import { Component } from '@angular/core';
import { AppStateService, C8yStepper, SetupComponent } from '@c8y/ngx-components';
import { Observable } from 'rxjs';
import { map, filter } from 'rxjs/operators';

@Component({
  selector: 'c8y-plugin-setup-stepper',
  templateUrl: './plugin-setup-stepper.component.html'
})
export class PluginSetupStepperComponent {
  appId$: Observable<string | number>;

  constructor(
    private appState: AppStateService,
    public stepper: C8yStepper,
    private setup: SetupComponent
  ) {
    this.appId$ = this.appState.currentApplication.pipe(
      map(app => app?.id),
      filter(appId => !!appId)
    );
  }

  next() {
    this.setup.stepCompleted(this.stepper.selectedIndex);
    this.stepper.next();
  }

  back() {
    this.stepper.previous();
  }
}
